import React from 'react'
import Affair from './Affair'
import {AffairType, FilterType} from './HW2'
import s from './Affairs.module.css'

type AffairsPropsType = { // need to fix any
  data: Array<AffairType>
  setFilter: (setFilter: FilterType) => void
  deleteAffairCallback: (_id: number) => void
}

function Affairs(props: AffairsPropsType) {
  const mappedAffairs = props.data.map((a: AffairType) => (
    <Affair // should work
      key={a._id} // кеи ОБЯЗАТЕЛЬНЫ в 99% - так что лучше их писать всегда при создании компонент в мапе
      affair={a}
      deleteAffairCallback={props.deleteAffairCallback}
    />
  ))

  const setAll = () => {
    props.setFilter('all')
  } // need to fix
  const setHigh = () => {
    props.setFilter('high')
  }
  const setMiddle = () => {
    props.setFilter('middle')
  }
  const setLow = () => {
    props.setFilter('low')
  }

  return (
    <div className={s.body}>
      <div className={s.title}>Homework 2:</div>
      <div>{mappedAffairs}</div>
      <div className={s.buttons}>
        <button className={s.button} onClick={setAll}><b>All</b></button>
        <button className={s.button} onClick={setHigh}><b>High</b></button>
        <button className={s.button} onClick={setMiddle}><b>Middle</b></button>
        <button className={s.button} onClick={setLow}><b>Low</b></button>
      </div>
    </div>
  )
}

export default Affairs
